import React, { useState, useEffect } from 'react';
import toast from 'react-hot-toast';
import { machineService } from '../services/api';
import { MachineStatistics, Machine } from '../types';
import {
  ChartBarIcon,
  DocumentTextIcon,
  ArrowDownTrayIcon,
} from '@heroicons/react/24/outline';

const ReportsPage: React.FC = () => {
  const [statistics, setStatistics] = useState<MachineStatistics | null>(null);
  const [machines, setMachines] = useState<Machine[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadReportData();
  }, []);

  const loadReportData = async () => {
    setLoading(true);
    try {
      const [stats, machineData] = await Promise.all([
        machineService.getDashboardStatistics(),
        machineService.getAllMachines(),
      ]);
      
      setStatistics(stats);
      setMachines(machineData);
    } catch (error) {
      console.error('Error loading report data:', error);
      toast.error('Failed to load report data');
    } finally {
      setLoading(false);
    }
  };

  const exportReport = (type: string) => {
    toast.success(`${type} report will be generated shortly`);
    // Here you would implement actual report generation
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Reports</h1>
        <p className="text-gray-600 mt-2">
          Generate and view system reports and analytics
        </p>
      </div>

      {/* Report Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex items-center">
            <ChartBarIcon className="h-8 w-8 text-blue-500" />
            <div className="ml-4">
              <h3 className="text-lg font-semibold text-gray-900">Machine Health Report</h3>
              <p className="text-sm text-gray-600 mt-1">
                Comprehensive health status across all machines
              </p>
            </div>
          </div>
          <div className="mt-4">
            <button
              onClick={() => exportReport('Machine Health')}
              className="flex items-center px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors"
            >
              <ArrowDownTrayIcon className="h-4 w-4 mr-2" />
              Download PDF
            </button>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex items-center">
            <DocumentTextIcon className="h-8 w-8 text-green-500" />
            <div className="ml-4">
              <h3 className="text-lg font-semibold text-gray-900">Maintenance Summary</h3>
              <p className="text-sm text-gray-600 mt-1">
                Scheduled and completed maintenance activities
              </p>
            </div>
          </div>
          <div className="mt-4">
            <button
              onClick={() => exportReport('Maintenance Summary')}
              className="flex items-center px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700 transition-colors"
            >
              <ArrowDownTrayIcon className="h-4 w-4 mr-2" />
              Download Excel
            </button>
          </div>
        </div>

        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex items-center">
            <ChartBarIcon className="h-8 w-8 text-purple-500" />
            <div className="ml-4">
              <h3 className="text-lg font-semibold text-gray-900">Analytics Report</h3>
              <p className="text-sm text-gray-600 mt-1">
                Performance trends and predictive insights
              </p>
            </div>
          </div>
          <div className="mt-4">
            <button
              onClick={() => exportReport('Analytics')}
              className="flex items-center px-4 py-2 bg-purple-600 text-white rounded hover:bg-purple-700 transition-colors"
            >
              <ArrowDownTrayIcon className="h-4 w-4 mr-2" />
              Generate Report
            </button>
          </div>
        </div>
      </div>

      {/* Summary Statistics */}
      {statistics && (
        <div className="bg-white rounded-lg shadow-sm p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">System Overview</h3>
          
          <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
            <div className="text-center">
              <div className="text-3xl font-bold text-blue-600">{statistics.totalMachines}</div>
              <div className="text-sm text-gray-600 mt-1">Total Machines</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-green-600">{statistics.activeMachines}</div>
              <div className="text-sm text-gray-600 mt-1">Active</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-yellow-600">{statistics.machinesInMaintenance}</div>
              <div className="text-sm text-gray-600 mt-1">In Maintenance</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-red-600">{statistics.criticalMachines}</div>
              <div className="text-sm text-gray-600 mt-1">Critical</div>
            </div>
          </div>

          <div className="mt-8 pt-6 border-t border-gray-200">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="text-center">
                <div className="text-2xl font-bold text-purple-600">
                  {statistics.averageHealthScore.toFixed(1)}%
                </div>
                <div className="text-sm text-gray-600 mt-1">Average Health Score</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-orange-600">{statistics.totalAlerts}</div>
                <div className="text-sm text-gray-600 mt-1">Active Alerts</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-red-600">{statistics.criticalAlerts}</div>
                <div className="text-sm text-gray-600 mt-1">Critical Alerts</div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Machine Health Distribution */}
      <div className="bg-white rounded-lg shadow-sm p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Machine Health Distribution</h3>
        
        {machines.length > 0 ? (
          <div className="space-y-4">
            {['CRITICAL', 'WARNING', 'GOOD', 'EXCELLENT'].map((category) => {
              const count = machines.filter(machine => {
                if (category === 'CRITICAL') return machine.healthScore < 60;
                if (category === 'WARNING') return machine.healthScore >= 60 && machine.healthScore < 75;
                if (category === 'GOOD') return machine.healthScore >= 75 && machine.healthScore < 90;
                return machine.healthScore >= 90;
              }).length;
              
              const percentage = machines.length > 0 ? (count / machines.length) * 100 : 0;
              
              return (
                <div key={category} className="flex items-center">
                  <div className="w-24 text-sm font-medium text-gray-700">{category}</div>
                  <div className="flex-1 mx-4">
                    <div className="w-full bg-gray-200 rounded-full h-4">
                      <div
                        className={`h-4 rounded-full ${
                          category === 'CRITICAL' ? 'bg-red-500' :
                          category === 'WARNING' ? 'bg-yellow-500' :
                          category === 'GOOD' ? 'bg-blue-500' : 'bg-green-500'
                        }`}
                        style={{ width: `${percentage}%` }}
                      ></div>
                    </div>
                  </div>
                  <div className="w-16 text-right text-sm text-gray-600">
                    {count} ({percentage.toFixed(1)}%)
                  </div>
                </div>
              );
            })}
          </div>
        ) : (
          <p className="text-gray-500 text-center py-8">No machines to analyze</p>
        )}
      </div>
    </div>
  );
};

export default ReportsPage;
