import React, { useState, useEffect } from 'react';
import toast from 'react-hot-toast';
import { machineService } from '../services/api';
import {
  Machine,
  MachineStatistics,
  MaintenanceAlert,
  MaintenanceSchedule,
} from '../types';
import {
  CogIcon,
  ExclamationTriangleIcon,
  CalendarIcon,
  ChartBarIcon,
  PlusIcon,
  EyeIcon,
} from '@heroicons/react/24/outline';
import {
  CheckCircleIcon as CheckCircleIconSolid,
  ExclamationCircleIcon as ExclamationCircleIconSolid,
} from '@heroicons/react/24/solid';

const Dashboard: React.FC = () => {
  const [statistics, setStatistics] = useState<MachineStatistics | null>(null);
  const [criticalMachines, setCriticalMachines] = useState<Machine[]>([]);
  const [activeAlerts, setActiveAlerts] = useState<MaintenanceAlert[]>([]);
  const [upcomingMaintenance, setUpcomingMaintenance] = useState<MaintenanceSchedule[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = async () => {
    setLoading(true);
    try {
      const [stats, critical, alerts, upcoming] = await Promise.all([
        machineService.getDashboardStatistics(),
        machineService.getMachinesRequiringAttention(),
        machineService.getActiveAlerts(),
        machineService.getUpcomingMaintenance(7), // Next 7 days
      ]);

      setStatistics(stats);
      setCriticalMachines(critical);
      setActiveAlerts(alerts);
      setUpcomingMaintenance(upcoming);
    } catch (error) {
      console.error('Error loading dashboard data:', error);
      toast.error('Failed to load dashboard data');
    } finally {
      setLoading(false);
    }
  };

  const getHealthScoreColor = (score: number): string => {
    if (score >= 90) return 'text-green-600 bg-green-50';
    if (score >= 75) return 'text-yellow-600 bg-yellow-50';
    if (score >= 60) return 'text-orange-600 bg-orange-50';
    return 'text-red-600 bg-red-50';
  };

  const getStatusColor = (status: string): string => {
    switch (status.toUpperCase()) {
      case 'ACTIVE': return 'text-green-600 bg-green-50';
      case 'MAINTENANCE': return 'text-yellow-600 bg-yellow-50';
      case 'INACTIVE': return 'text-gray-600 bg-gray-50';
      case 'DECOMMISSIONED': return 'text-red-600 bg-red-50';
      default: return 'text-gray-600 bg-gray-50';
    }
  };

  const getSeverityColor = (severity: string): string => {
    switch (severity.toUpperCase()) {
      case 'CRITICAL': case 'EMERGENCY': return 'text-red-600 bg-red-50 border-red-200';
      case 'WARNING': return 'text-yellow-600 bg-yellow-50 border-yellow-200';
      case 'INFO': return 'text-blue-600 bg-blue-50 border-blue-200';
      default: return 'text-gray-600 bg-gray-50 border-gray-200';
    }
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-8">
      {/* Page Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
        <p className="text-gray-600 mt-2">
          Overview of machine health and maintenance activities
        </p>
      </div>

      {/* Statistics Grid */}
      {statistics && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6 gap-6">
          <div className="bg-white rounded-lg shadow-sm p-6 border-t-4 border-blue-500">
            <div className="flex items-center">
              <CogIcon className="h-8 w-8 text-blue-500" />
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Total Machines</p>
                <p className="text-2xl font-bold text-gray-900">{statistics.totalMachines}</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6 border-t-4 border-green-500">
            <div className="flex items-center">
              <CheckCircleIconSolid className="h-8 w-8 text-green-500" />
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Active</p>
                <p className="text-2xl font-bold text-gray-900">{statistics.activeMachines}</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6 border-t-4 border-yellow-500">
            <div className="flex items-center">
              <ExclamationTriangleIcon className="h-8 w-8 text-yellow-500" />
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">In Maintenance</p>
                <p className="text-2xl font-bold text-gray-900">{statistics.machinesInMaintenance}</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6 border-t-4 border-red-500">
            <div className="flex items-center">
              <ExclamationCircleIconSolid className="h-8 w-8 text-red-500" />
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Critical</p>
                <p className="text-2xl font-bold text-gray-900">{statistics.criticalMachines}</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6 border-t-4 border-purple-500">
            <div className="flex items-center">
              <ChartBarIcon className="h-8 w-8 text-purple-500" />
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Avg Health</p>
                <p className="text-2xl font-bold text-gray-900">{statistics.averageHealthScore.toFixed(1)}%</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6 border-t-4 border-orange-500">
            <div className="flex items-center">
              <ExclamationTriangleIcon className="h-8 w-8 text-orange-500" />
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Active Alerts</p>
                <p className="text-2xl font-bold text-gray-900">{statistics.totalAlerts}</p>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Main Content Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
        {/* Critical Machines */}
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900">
              Critical Machines
            </h3>
            <span className="bg-red-100 text-red-800 text-sm font-medium px-2 py-1 rounded-full">
              {criticalMachines.length}
            </span>
          </div>
          
          {criticalMachines.length === 0 ? (
            <div className="text-center py-8">
              <CheckCircleIconSolid className="h-16 w-16 text-green-500 mx-auto mb-4" />
              <p className="text-gray-600">All machines are operating normally!</p>
            </div>
          ) : (
            <div className="space-y-3 max-h-80 overflow-y-auto">
              {criticalMachines.map((machine) => (
                <div
                  key={machine.id}
                  className="border-l-4 border-red-500 bg-red-50 p-3 rounded-r-lg"
                >
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <p className="font-semibold text-gray-900">{machine.machineCode}</p>
                      <p className="text-sm text-gray-600">{machine.machineName}</p>
                      <div className="flex items-center mt-1">
                        <span className="text-xs text-gray-500">
                          Health: {machine.healthScore.toFixed(1)}%
                        </span>
                        <span className="mx-2 text-gray-300"></span>
                        <span className={`text-xs px-2 py-1 rounded-full ${getStatusColor(machine.status)}`}>
                          {machine.status}
                        </span>
                      </div>
                    </div>
                    <button className="text-blue-600 hover:text-blue-800">
                      <EyeIcon className="h-5 w-5" />
                    </button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Active Alerts */}
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900">
              Active Alerts
            </h3>
            <span className="bg-yellow-100 text-yellow-800 text-sm font-medium px-2 py-1 rounded-full">
              {activeAlerts.length}
            </span>
          </div>
          
          {activeAlerts.length === 0 ? (
            <div className="text-center py-8">
              <CheckCircleIconSolid className="h-16 w-16 text-green-500 mx-auto mb-4" />
              <p className="text-gray-600">No active alerts!</p>
            </div>
          ) : (
            <div className="space-y-3 max-h-80 overflow-y-auto">
              {activeAlerts.map((alert) => (
                <div
                  key={alert.id}
                  className={`border rounded-lg p-3 ${getSeverityColor(alert.severity)}`}
                >
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <p className="font-semibold">{alert.title}</p>
                      <p className="text-sm mt-1">{alert.description}</p>
                      <div className="flex items-center mt-2">
                        <span className="text-xs">
                          {alert.machine?.machineCode}  {new Date(alert.alertTimestamp).toLocaleDateString()}
                        </span>
                      </div>
                    </div>
                    <span className={`text-xs px-2 py-1 rounded-full font-medium ${getSeverityColor(alert.severity)}`}>
                      {alert.severity}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Upcoming Maintenance */}
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900">
              Upcoming Maintenance
            </h3>
            <span className="bg-blue-100 text-blue-800 text-sm font-medium px-2 py-1 rounded-full">
              {upcomingMaintenance.length}
            </span>
          </div>
          
          {upcomingMaintenance.length === 0 ? (
            <div className="text-center py-8">
              <CalendarIcon className="h-16 w-16 text-gray-400 mx-auto mb-4" />
              <p className="text-gray-600">No maintenance scheduled for the next week!</p>
            </div>
          ) : (
            <div className="space-y-3 max-h-80 overflow-y-auto">
              {upcomingMaintenance.map((schedule) => (
                <div
                  key={schedule.id}
                  className="border-l-4 border-blue-500 bg-blue-50 p-3 rounded-r-lg"
                >
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <p className="font-semibold text-gray-900">{schedule.machine?.machineCode}</p>
                      <p className="text-sm text-gray-600">{schedule.machine?.machineName}</p>
                      <div className="flex items-center mt-1">
                        <span className="text-xs text-gray-500">
                          {new Date(schedule.scheduledDate).toLocaleDateString()}
                        </span>
                        <span className="mx-2 text-gray-300"></span>
                        <span className="text-xs text-gray-500">
                          {schedule.estimatedDurationHours}h
                        </span>
                      </div>
                    </div>
                    <span className={`text-xs px-2 py-1 rounded-full ${
                      schedule.priority === 'HIGH' || schedule.priority === 'CRITICAL' 
                        ? 'text-red-600 bg-red-50' 
                        : 'text-blue-600 bg-blue-50'
                    }`}>
                      {schedule.priority}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Quick Actions */}
      <div className="bg-white rounded-lg shadow-sm p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">
          Quick Actions
        </h3>
        <div className="flex flex-wrap gap-4">
          <button
            onClick={() => window.location.href = '/machines/add'}
            className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          >
            <PlusIcon className="h-5 w-5 mr-2" />
            Add Machine
          </button>
          <button
            onClick={() => window.location.href = '/machines'}
            className="flex items-center px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
          >
            <CogIcon className="h-5 w-5 mr-2" />
            View All Machines
          </button>
          <button
            onClick={() => window.location.href = '/alerts'}
            className="flex items-center px-4 py-2 border border-yellow-300 text-yellow-700 bg-yellow-50 rounded-lg hover:bg-yellow-100 transition-colors"
          >
            <ExclamationTriangleIcon className="h-5 w-5 mr-2" />
            Review Alerts ({activeAlerts.length})
          </button>
          <button
            onClick={() => window.location.href = '/maintenance'}
            className="flex items-center px-4 py-2 border border-green-300 text-green-700 bg-green-50 rounded-lg hover:bg-green-100 transition-colors"
          >
            <CalendarIcon className="h-5 w-5 mr-2" />
            Schedule Maintenance
          </button>
          <a
            href="http://localhost:8080/swagger"
            target="_blank"
            rel="noopener noreferrer"
            className="flex items-center px-4 py-2 border border-purple-300 text-purple-700 bg-purple-50 rounded-lg hover:bg-purple-100 transition-colors"
          >
            <ChartBarIcon className="h-5 w-5 mr-2" />
            API Documentation
          </a>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
