import axios from 'axios';
import {
  Machine,
  MachineType,
  MachineStatistics,
  MachineHealthReading,
  MaintenanceAlert,
  MaintenanceSchedule,
  CreateMachineRequest,
  UpdateHealthScoreRequest,
  UpdateStatusRequest,
  HealthCategory
} from '../types';

const API_BASE_URL = 'http://localhost:8080/api';

const api = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 10000,
});

// Add request interceptor for loading states
api.interceptors.request.use((config) => {
  console.log(`Making ${config.method?.toUpperCase()} request to ${config.url}`);
  return config;
});

// Add response interceptor for error handling
api.interceptors.response.use(
  (response) => response,
  (error) => {
    console.error('API Error:', error);
    return Promise.reject(error);
  }
);

export const machineService = {
  // Get all machines
  getAllMachines: async (): Promise<Machine[]> => {
    const response = await api.get<Machine[]>('/machines');
    return response.data;
  },

  // Get machine by ID
  getMachineById: async (id: number): Promise<Machine> => {
    const response = await api.get<Machine>(`/machines/${id}`);
    return response.data;
  },

  // Get machine by code
  getMachineByCode: async (code: string): Promise<Machine> => {
    const response = await api.get<Machine>(`/machines/code/${code}`);
    return response.data;
  },

  // Create new machine
  createMachine: async (machine: CreateMachineRequest): Promise<Machine> => {
    const response = await api.post<Machine>('/machines', machine);
    return response.data;
  },

  // Update existing machine
  updateMachine: async (id: number, machine: Partial<Machine>): Promise<Machine> => {
    const response = await api.put<Machine>(`/machines/${id}`, machine);
    return response.data;
  },

  // Delete machine
  deleteMachine: async (id: number): Promise<void> => {
    await api.delete(`/machines/${id}`);
  },

  // Get machines requiring attention
  getMachinesRequiringAttention: async (): Promise<Machine[]> => {
    const response = await api.get<Machine[]>('/machines/attention');
    return response.data;
  },

  // Get machines by health category
  getMachinesByHealthCategory: async (category: HealthCategory): Promise<Machine[]> => {
    const response = await api.get<Machine[]>(`/machines/health/${category}`);
    return response.data;
  },

  // Search machines
  searchMachines: async (term: string): Promise<Machine[]> => {
    const response = await api.get<Machine[]>(`/machines/search?term=${encodeURIComponent(term)}`);
    return response.data;
  },

  // Get machines by status
  getMachinesByStatus: async (status: string): Promise<Machine[]> => {
    const response = await api.get<Machine[]>(`/machines/status/${status}`);
    return response.data;
  },

  // Get machine health readings
  getMachineHealthReadings: async (id: number): Promise<MachineHealthReading[]> => {
    const response = await api.get<MachineHealthReading[]>(`/machines/${id}/health-readings`);
    return response.data;
  },

  // Get machine maintenance schedules
  getMachineMaintenanceSchedules: async (id: number): Promise<MaintenanceSchedule[]> => {
    const response = await api.get<MaintenanceSchedule[]>(`/machines/${id}/schedules`);
    return response.data;
  },

  // Get machine alerts
  getMachineAlerts: async (id: number): Promise<MaintenanceAlert[]> => {
    const response = await api.get<MaintenanceAlert[]>(`/machines/${id}/alerts`);
    return response.data;
  },

  // Update machine health score
  updateHealthScore: async (id: number, request: UpdateHealthScoreRequest): Promise<Machine> => {
    const response = await api.patch<Machine>(`/machines/${id}/health-score`, request);
    return response.data;
  },

  // Update machine status
  updateMachineStatus: async (id: number, request: UpdateStatusRequest): Promise<Machine> => {
    const response = await api.patch<Machine>(`/machines/${id}/status`, request);
    return response.data;
  },

  // Get dashboard statistics
  getDashboardStatistics: async (): Promise<MachineStatistics> => {
    const response = await api.get<MachineStatistics>('/machines/statistics');
    return response.data;
  },

  // Get recent health readings across all machines
  getRecentHealthReadings: async (hours: number = 24): Promise<MachineHealthReading[]> => {
    const response = await api.get<MachineHealthReading[]>(`/machines/recent-readings?hours=${hours}`);
    return response.data;
  },

  // Get active alerts across all machines
  getActiveAlerts: async (): Promise<MaintenanceAlert[]> => {
    const response = await api.get<MaintenanceAlert[]>('/machines/active-alerts');
    return response.data;
  },

  // Get upcoming maintenance across all machines
  getUpcomingMaintenance: async (days: number = 30): Promise<MaintenanceSchedule[]> => {
    const response = await api.get<MaintenanceSchedule[]>(`/machines/upcoming-maintenance?days=${days}`);
    return response.data;
  },
};

// Health check endpoint
export const healthCheck = async (): Promise<{ status: string }> => {
  const response = await api.get('/health');
  return response.data;
};

export default api;
