import React, { useState, useEffect } from 'react';
import toast from 'react-hot-toast';
import { machineService } from '../services/api';
import { MaintenanceAlert } from '../types';
import {
  ExclamationTriangleIcon,
  CheckCircleIcon,
  MagnifyingGlassIcon,
  FunnelIcon,
} from '@heroicons/react/24/outline';

const AlertsPage: React.FC = () => {
  const [alerts, setAlerts] = useState<MaintenanceAlert[]>([]);
  const [filteredAlerts, setFilteredAlerts] = useState<MaintenanceAlert[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedSeverity, setSelectedSeverity] = useState('');
  const [showAcknowledged, setShowAcknowledged] = useState(false);

  useEffect(() => {
    loadAlerts();
  }, []);

  useEffect(() => {
    filterAlerts();
  }, [alerts, searchTerm, selectedSeverity, showAcknowledged]);

  const loadAlerts = async () => {
    setLoading(true);
    try {
      const data = await machineService.getActiveAlerts();
      setAlerts(data);
    } catch (error) {
      console.error('Error loading alerts:', error);
      toast.error('Failed to load alerts');
    } finally {
      setLoading(false);
    }
  };

  const filterAlerts = () => {
    let filtered = alerts;

    if (searchTerm) {
      filtered = filtered.filter(alert =>
        alert.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        alert.description?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        alert.machine?.machineCode.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (selectedSeverity) {
      filtered = filtered.filter(alert => alert.severity === selectedSeverity);
    }

    if (!showAcknowledged) {
      filtered = filtered.filter(alert => !alert.acknowledged);
    }

    setFilteredAlerts(filtered);
  };

  const getSeverityColor = (severity: string): string => {
    switch (severity.toUpperCase()) {
      case 'CRITICAL': case 'EMERGENCY': return 'bg-red-50 border-red-200 text-red-800';
      case 'WARNING': return 'bg-yellow-50 border-yellow-200 text-yellow-800';
      case 'INFO': return 'bg-blue-50 border-blue-200 text-blue-800';
      default: return 'bg-gray-50 border-gray-200 text-gray-800';
    }
  };

  const getSeverityIcon = (severity: string) => {
    switch (severity.toUpperCase()) {
      case 'CRITICAL': case 'EMERGENCY': return <ExclamationTriangleIcon className="h-5 w-5 text-red-600" />;
      case 'WARNING': return <ExclamationTriangleIcon className="h-5 w-5 text-yellow-600" />;
      default: return <CheckCircleIcon className="h-5 w-5 text-blue-600" />;
    }
  };

  const formatDate = (dateString: string): string => {
    const date = new Date(dateString);
    const now = new Date();
    const diffMs = now.getTime() - date.getTime();
    const diffHours = Math.floor(diffMs / (1000 * 60 * 60));
    const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24));

    if (diffHours < 1) {
      const diffMinutes = Math.floor(diffMs / (1000 * 60));
      return `${diffMinutes} minutes ago`;
    } else if (diffHours < 24) {
      return `${diffHours} hours ago`;
    } else {
      return `${diffDays} days ago`;
    }
  };

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Alerts</h1>
        <p className="text-gray-600 mt-2">
          Monitor and manage system alerts and notifications
        </p>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-lg shadow-sm p-6">
        <div className="flex flex-col sm:flex-row gap-4">
          <div className="flex-1">
            <div className="relative">
              <MagnifyingGlassIcon className="h-5 w-5 absolute left-3 top-3 text-gray-400" />
              <input
                type="text"
                placeholder="Search alerts by title, description, or machine..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
          </div>
          <div className="sm:w-48">
            <select
              value={selectedSeverity}
              onChange={(e) => setSelectedSeverity(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="">All Severities</option>
              <option value="CRITICAL">Critical</option>
              <option value="WARNING">Warning</option>
              <option value="INFO">Info</option>
              <option value="EMERGENCY">Emergency</option>
            </select>
          </div>
          <div className="flex items-center">
            <label className="flex items-center">
              <input
                type="checkbox"
                checked={showAcknowledged}
                onChange={(e) => setShowAcknowledged(e.target.checked)}
                className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
              />
              <span className="ml-2 text-sm text-gray-700">Show acknowledged</span>
            </label>
          </div>
        </div>
      </div>

      {/* Alerts List */}
      <div className="bg-white rounded-lg shadow-sm">
        <div className="px-6 py-4 border-b border-gray-200">
          <div className="flex items-center justify-between">
            <h3 className="text-lg font-semibold text-gray-900">
              Active Alerts ({filteredAlerts.length})
            </h3>
            <div className="flex items-center space-x-2">
              <FunnelIcon className="h-5 w-5 text-gray-400" />
              <span className="text-sm text-gray-500">
                {selectedSeverity || 'All'}  {showAcknowledged ? 'All' : 'Unacknowledged'}
              </span>
            </div>
          </div>
        </div>

        {loading ? (
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
          </div>
        ) : filteredAlerts.length === 0 ? (
          <div className="text-center py-12">
            <CheckCircleIcon className="h-16 w-16 text-green-400 mx-auto mb-4" />
            <p className="text-gray-600 text-lg">No alerts found</p>
            <p className="text-gray-500 mt-1">
              {searchTerm || selectedSeverity ? 'Try adjusting your filters' : 'All systems are running normally'}
            </p>
          </div>
        ) : (
          <div className="divide-y divide-gray-200">
            {filteredAlerts.map((alert) => (
              <div
                key={alert.id}
                className={`p-6 border-l-4 ${
                  alert.severity === 'CRITICAL' || alert.severity === 'EMERGENCY'
                    ? 'border-red-500 bg-red-50'
                    : alert.severity === 'WARNING'
                    ? 'border-yellow-500 bg-yellow-50'
                    : 'border-blue-500 bg-blue-50'
                }`}
              >
                <div className="flex justify-between items-start">
                  <div className="flex-1">
                    <div className="flex items-center">
                      {getSeverityIcon(alert.severity)}
                      <h4 className="text-lg font-semibold text-gray-900 ml-2">
                        {alert.title}
                      </h4>
                      <span className={`ml-4 px-2 py-1 text-xs font-medium rounded-full ${getSeverityColor(alert.severity)}`}>
                        {alert.severity}
                      </span>
                    </div>
                    
                    {alert.description && (
                      <p className="text-gray-700 mt-2">{alert.description}</p>
                    )}
                    
                    <div className="flex items-center mt-4 text-sm text-gray-600">
                      <span className="font-medium">Machine:</span>
                      <span className="ml-1 text-blue-600">
                        {alert.machine?.machineCode} - {alert.machine?.machineName}
                      </span>
                      <span className="mx-4"></span>
                      <span>{formatDate(alert.alertTimestamp)}</span>
                      {alert.acknowledged && (
                        <>
                          <span className="mx-4"></span>
                          <CheckCircleIcon className="h-4 w-4 text-green-500 mr-1" />
                          <span className="text-green-600">Acknowledged</span>
                          {alert.acknowledgedBy && (
                            <span className="ml-1">by {alert.acknowledgedBy}</span>
                          )}
                        </>
                      )}
                    </div>
                  </div>

                  <div className="flex space-x-2 ml-4">
                    {!alert.acknowledged && (
                      <button className="px-3 py-1 text-sm bg-green-600 text-white rounded hover:bg-green-700 transition-colors">
                        Acknowledge
                      </button>
                    )}
                    {!alert.resolved && (
                      <button className="px-3 py-1 text-sm bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors">
                        Resolve
                      </button>
                    )}
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default AlertsPage;
