import React, { useState, useEffect } from 'react';
import toast from 'react-hot-toast';
import { machineService } from '../services/api';
import { MaintenanceSchedule } from '../types';
import {
  CalendarIcon,
  ClockIcon,
  UserIcon,
  WrenchScrewdriverIcon,
} from '@heroicons/react/24/outline';

const MaintenancePage: React.FC = () => {
  const [upcomingMaintenance, setUpcomingMaintenance] = useState<MaintenanceSchedule[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadMaintenanceData();
  }, []);

  const loadMaintenanceData = async () => {
    setLoading(true);
    try {
      const data = await machineService.getUpcomingMaintenance(30); // Next 30 days
      setUpcomingMaintenance(data);
    } catch (error) {
      console.error('Error loading maintenance data:', error);
      toast.error('Failed to load maintenance data');
    } finally {
      setLoading(false);
    }
  };

  const getPriorityColor = (priority: string): string => {
    switch (priority.toUpperCase()) {
      case 'CRITICAL': return 'bg-red-100 text-red-800';
      case 'HIGH': return 'bg-orange-100 text-orange-800';
      case 'MEDIUM': return 'bg-yellow-100 text-yellow-800';
      case 'LOW': return 'bg-green-100 text-green-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusColor = (status: string): string => {
    switch (status.toUpperCase()) {
      case 'SCHEDULED': return 'bg-blue-100 text-blue-800';
      case 'IN_PROGRESS': return 'bg-yellow-100 text-yellow-800';
      case 'COMPLETED': return 'bg-green-100 text-green-800';
      case 'CANCELLED': return 'bg-red-100 text-red-800';
      case 'OVERDUE': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <div className="space-y-6">
      {/* Page Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Maintenance</h1>
        <p className="text-gray-600 mt-2">
          Schedule and track maintenance activities
        </p>
      </div>

      {/* Upcoming Maintenance */}
      <div className="bg-white rounded-lg shadow-sm">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-semibold text-gray-900">
            Upcoming Maintenance ({upcomingMaintenance.length})
          </h3>
        </div>

        {loading ? (
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
          </div>
        ) : upcomingMaintenance.length === 0 ? (
          <div className="text-center py-12">
            <WrenchScrewdriverIcon className="h-16 w-16 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-600 text-lg">No maintenance scheduled</p>
            <p className="text-gray-500 mt-1">All systems are up to date</p>
          </div>
        ) : (
          <div className="divide-y divide-gray-200">
            {upcomingMaintenance.map((schedule) => (
              <div key={schedule.id} className="p-6">
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center">
                      <WrenchScrewdriverIcon className="h-5 w-5 text-gray-500 mr-2" />
                      <h4 className="text-lg font-semibold text-gray-900">
                        {schedule.machine?.machineCode} - {schedule.machine?.machineName}
                      </h4>
                    </div>
                    
                    <p className="text-gray-700 mt-2">{schedule.taskDescription}</p>
                    
                    <div className="flex items-center mt-4 space-x-6 text-sm text-gray-600">
                      <div className="flex items-center">
                        <CalendarIcon className="h-4 w-4 mr-1" />
                        <span>{new Date(schedule.scheduledDate).toLocaleDateString()}</span>
                      </div>
                      {schedule.estimatedDurationHours && (
                        <div className="flex items-center">
                          <ClockIcon className="h-4 w-4 mr-1" />
                          <span>{schedule.estimatedDurationHours}h</span>
                        </div>
                      )}
                      {schedule.assignedTechnician && (
                        <div className="flex items-center">
                          <UserIcon className="h-4 w-4 mr-1" />
                          <span>{schedule.assignedTechnician}</span>
                        </div>
                      )}
                    </div>
                    
                    <div className="flex items-center mt-2 space-x-2">
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${getPriorityColor(schedule.priority)}`}>
                        {schedule.priority} Priority
                      </span>
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(schedule.status)}`}>
                        {schedule.status}
                      </span>
                      <span className="px-2 py-1 text-xs bg-gray-100 text-gray-800 rounded-full">
                        {schedule.maintenanceType}
                      </span>
                    </div>
                  </div>

                  <div className="flex space-x-2 ml-4">
                    <button className="px-3 py-1 text-sm bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors">
                      Edit
                    </button>
                    <button className="px-3 py-1 text-sm border border-gray-300 text-gray-700 rounded hover:bg-gray-50 transition-colors">
                      Reschedule
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default MaintenancePage;
